<template>
  <div id="contact">
    <el-tabs v-model="activeName" @tab-click="handleClick" type="border-card">
      <el-tab-pane label="方案1" name="first">
        <type-a></type-a>
      </el-tab-pane>
      <el-tab-pane label="方案2" name="second">
        <type-b></type-b>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { Tabs, TabPane} from 'element-ui';
import typeA from '../component/typeA.vue'
import typeB from '../component/typeB.vue'
export default {
  name:"contact",
  components:{
      [Tabs.name]:Tabs,
      [TabPane.name]:TabPane,
      'type-a':typeA,
      'type-b':typeB
    },
  data() {
    return {
      activeName: 'first'
    };
  },
  methods: {
    handleClick(tab, event) {
      console.log(tab, event);
    }
  }
}
</script>

<style>

</style>



